<template>
  <Transition name="modal">
    <div class="modal" @click="closeModal">
      <div class="modal__block" @click.stop>
        <h3 class="modal__block_title">
          {{ edit ? 'Изменить заметку' : 'Добавить заметку' }}
          
        </h3>
        <label>
          <span>Title</span>

          <input type="text" placeholder="title" v-model="title" />
        </label>
        <label>
          <span>Content</span>
          <textarea placeholder="Content" v-model="text"></textarea>
        </label>
        <div class="modal__block_btns">
          <button class="modal__block_btns-btn del" @click="closeModal">
            Отмена
          </button>

          <button
            v-if="!edit"
            class="modal__block_btns-btn edit"
            @click="addNote"
          >
            Добавить
          </button>

          <button v-else class="modal__block_btns-btn edit" @click="changeNote">
            изменить
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
export default {
  data() {
    return {
      title: '',
      text: '',
    }
  },
  methods: {
    closeModal() {
      this.$emit('close')
      this.title = this.text = ''
    },
    addNote() {
      if (this.title != '' && this.text != '') {
        const note = {
          id: uuidv4(),
          title: this.title,
          text: this.text,
          /* 29.09.23 */
          date: new Date().toLocaleDateString(),
        }
        this.$emit('addNote', note)
        this.closeModal()
      }
    },
    changeNote() {
      const newNote = {
        id: this.editedNote.id,
        title: this.title,
        text: this.text,
        date: new Date().toLocaleDateString(),
      }
      this.$emit('changedNote', newNote)
      this.closeModal()
    },
  },
  props: {
    edit: Boolean,
    editedNote: Object,
  },
}
</script>
